<template>
  <div class="play-container" v-show="playlist.length > 0">
    <transition name="normal" @enter="enter" @after-enter="afterEnter">
      <!-- @leave="leave"
      @after-leave="afterLeave"-->
      <div class="normal-play" v-show="fullScreen">
        <div class="bg">
          <img :src="currentSong.image" width="100%" height="100%">
        </div>
        <div class="top">
          <div class="back" @click="back">
            <i class="icon-back"></i>
          </div>
          <h1 class="title">{{ currentSong.name }}</h1>
          <h2 class="name">{{ currentSong.singer }}</h2>
        </div>
        <div class="middle">
          <div class="middle-l">
            <div class="cd" ref="cd">
              <div :class="_cd">
                <img :src="currentSong.image" alt>
              </div>
            </div>
            <div class="lyric">
              <div class="lyric-content">没有说的儒雅</div>
            </div>
          </div>
          <div class="middle-r"></div>
        </div>
        <div class="bottom">
          <div class="b-t">
            <span></span>
            <span></span>
          </div>
          <div class="b-m">
            <span class="atTime">00:00</span>
            <div class="bar-wrapper">
              <div class="bar">
                <div class="bar-inner">
                  <div class="bar-start"></div>
                  <div class="bar-btn">
                    <div class="bar-btn-content"></div>
                  </div>
                </div>
              </div>
            </div>
            <span class="totalTime">22:22</span>
          </div>
          <div class="b-b">
            <div class="left">
              <i class="icon-sequence"></i>
            </div>
            <div class="left" @click="prev">
              <i class="icon-prev"></i>
            </div>
            <div class="center" ref="toggle" @click="togglePlaying">
              <i :class="_playIcon"></i>
            </div>
            <div class="right" @click="next">
              <i class="icon-next"></i>
            </div>
            <div class="right">
              <i class="icon icon-not-favorite"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <div class="mini-play" v-show="!fullScreen" @click="open">
      <div class="head">
        <img :src="currentSong.image" width="40px" height="40px">
      </div>
      <div class="text">
        <h2>{{ currentSong.name }}</h2>
        <p>{{ currentSong.singer }}</p>
      </div>
      <div class="control">
        <circle-wrap>
          <i :class="'icon-mini '+_miniIcon"></i>
        </circle-wrap>
      </div>
      <div class="control">
        <i class="icon-playlist"></i>
      </div>
    </div>
    <!-- <div class="play-list">
      <div class="list-wrap">
        <div class="head">
          <i class="icon icon-random"></i>
          <span class>随机播放</span>
          <span class>
            <i class="icon-clear"></i>
          </span>
        </div>
        <div class="content">
          <ul>
            <li>
              <i class></i>
              <span class="txt"></span>
              <span class="like">
                <i class="icon-not-favorite"></i>
              </span>
              <span class="del">
                <i class="icon-delete"></i>
              </span>
            </li>
          </ul>
        </div>
        <div class="bottom">
          <div>
            <i class="icon-add"></i>
            <span>添加歌曲到队列</span>
          </div>
        </div>
        <div class="close">
          <span>关闭</span>
        </div>
      </div>

      <div class="confrim"></div>
      <div class="add"></div>
    </div>-->
    <audio :src="currentSong.url" autoplay @play="ready" @ended="end"></audio>
  </div>
</template>

<script>
import circleWrap from "../../base/circle/circle";
import { mapGetters, mapMutations } from "vuex";
import animations from "create-keyframe-animation";
import { playerMixin } from "../common/mixins";
export default {
  mixins: [playerMixin],
  data() {
    return {
      songReady: false
    };
  },

  computed: {
    _cd() {
      return this.playing ? "play" : "play pause";
    },
    _playIcon() {
      return this.playing ? "icon-pause" : "icon-play";
    },
    _miniIcon() {
      return this.playing ? "icon-pause-mini" : "icon-play-mini";
    },
    ...mapGetters(["fullScreen", "playing", "currentSong", "currentIndex"])
  },
  methods: {
    togglePlaying() {
      if (!this.songReady) {
        return;
      }
      this.setPlayingState(!this.playing);
    },
    end() {
      if (this.mode === 0) {
        this.loop();
      }
    },
    loop() {
      this.$refs.audio.currentTime = 0;
      this.$refs.audio.play();
      this.setPlayingState(true);
    },
    next() {
      if (!this.songReady) {
        return;
      }
      if (this.playlist.length === 1) {
        this.loop();
        return;
      } else {
        let index = parseInt(this.currentIndex) + 1;
        console.log(this.currentIndex)
        if (index === this.playlist.length) {
          index = 0;
        }
        console.log(index)
        this.setCurrentIndex(index);
        if (!this.playing) {
          this.togglePlaying();
        }
      }
      this.songReady = false;
    },
    prev() {
      if (!this.songReady) {
        return;
      }
      if (this.playlist.length === 1) {
        this.loop();
        return;
      } else {
        let index = parseInt(this.currentIndex - 1);
        if (index === -1) {
          index = this.playlist.length - 1;
        }
        this.setCurrentIndex(index);
        if (!this.playing) {
          this.togglePlaying();
        }
      }
      this.songReady = false;
    },
    ready() {
      this.songReady = true;
    },
    open() {
      this.setScreen(true);
    },
    enter(el, done) {
      let { x, y, scale } = this.getPos();

      let animation = {
        0: {
          transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
        },
        60: {
          transform: `translate3d(0,0,0) scale(1.1)`
        },
        100: {
          transform: `translate3d(0,0,0) scale(1)`
        }
      };

      animations.registerAnimation({
        name: "move",
        animation,
        presets: {
          duration: 400,
          easing: "linear"
        }
      });

      animations.runAnimation(this.$refs.cd, "move", done);
    },
    afterEnter() {
      animations.unregisterAnimation("move");
      this.$refs.cd.style.animation = "";
    },
    leave(el, done) {
      this.$refs.cd.style.transition = "all 0.4s";
      let { x, y, scale } = this.getPos();
      this.$refs.cd.style[
        transform
      ] = `translate3d(${x}px,${y}px,0) scale(${scale})`;
      this.$refs.cd.addEventListener("transitionend", done);
    },
    afterLeave() {
      this.$refs.cd.style.transition = "";
      this.$refs.cd.style[transform] = "";
    },
    getPos() {
      let targetWidth = 40;
      let paddingLeft = 40;
      let paddingBottom = 30;
      let paddingTop = 80;
      let width = window.innerWidth * 0.8;
      let scale = targetWidth / width;
      let x = -(window.innerWidth / 2 - paddingLeft);
      let y = window.innerHeight - paddingTop - width / 2 - paddingBottom;
      return {
        x,
        y,
        scale
      };
    },
    back() {
      this.setScreen(false);
    },

    ...mapMutations({
      setScreen: "SET_FULL_SCREEN"
    })
  },
  components: {
    circleWrap
  }
};
</script>
<style lang="" scoped>
.normal-enter,
.normal-leave-to {
  opacity: 0;
}
.normal-enter .top {
  transform: translate3d(0, -100px, 0);
}
.normal-leave-to .bottom {
  transform: translate3d(0, 100px, 0);
}
.normal-enter-active,
.normal-leave-active {
  transition: all 0.4s;
}
.normal-enter-active .top,
.normal-enter-active .bottom {
  transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
}

.normal-play {
  position: fixed;
  z-index: 222;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
}
.normal-play .bg {
  filter: blur(20px);
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.normal-play .top {
  position: relative;
  margin-bottom: 25px;
}
.normal-play .top .back {
  position: absolute;
  top: 0;
  left: 6px;
  z-index: 40;
}
.normal-play .top .back i {
  color: #31c27c;
  padding: 9px;
  display: block;
  font-size: 22px;
  transform: rotate(-90deg);
}
.normal-play .top .title {
  width: 70%;
  margin: auto;
  line-height: 40px;
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 18px;
  color: #000;
}
.normal-play .top .name {
  line-height: 20px;
  font-size: 14px;
  color: #000;
  text-align: center;
}
.normal-play .middle {
  position: fixed;
  width: 100%;
  top: 80px;
  bottom: 170px;
  white-space: nowrap;
  font-size: 0;
}
.normal-play .middle .middle-l {
  display: inline-block;
  vertical-align: top;
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 80%;
}
.normal-play .middle .middle-l .cd {
  position: absolute;
  left: 10%;
  top: 0;
  width: 80%;
  height: 100%;
}
.normal-play .middle .middle-l .cd .play {
  animation: rotate 20s linear infinite;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 10px solid rgba(255, 255, 255, 0.1);
  background-color: #31c27c;
}
.normal-play .middle .middle-l .cd .play.pause {
  animation-play-state: paused;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.normal-play .middle .middle-l .cd .play img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.normal-play .middle .middle-l .lyric {
  width: 80%;
  margin: 30px auto 0 auto;
  overflow: hidden;
  text-align: center;
}
.normal-play .middle .middle-l .lyric-content {
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #0d5d37;
}

.normal-play .bottom {
  position: absolute;
  bottom: 50px;
  width: 100%;
}
.normal-play .bottom .b-t {
  text-align: center;
  font-size: 0;
}
.normal-play .bottom .b-t span {
  display: inline-block;
  vertical-align: middle;
  margin: 0 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: rgba(46, 185, 76, 0.897);
}
.normal-play .bottom .b-m {
  display: flex;
  /* align-items: center; */
  width: 80%;
  margin: 0px auto;
  padding: 10px 0;
}
.normal-play .bottom span {
  color: #31c27c;
  font-size: 12px;
  flex: 0 0 36px;
  line-height: 30px;
  width: 30px;
}
.normal-play .bottom .b-m .atTime {
  text-align: left;
}
.normal-play .bottom .b-m .bar-wrapper {
  flex: 1;
}
.normal-play .bottom .b-m .bar-wrapper .bar {
  height: 3px;
}
.normal-play .bottom .b-m .bar-wrapper .bar .bar-inner {
  position: relative;
  top: 13px;
  height: 4px;
  background: rgba(0, 0, 0, 0.3);
}
.normal-play .bottom .b-m .bar-wrapper .bar .bar-inner .bar-start {
  position: absolute;
  height: 100%;
  background: #31c27c;
}
.normal-play .bottom .b-m .bar-wrapper .bar .bar-inner .bar-btn {
  position: absolute;
  left: -8px;
  top: -13px;
  width: 30px;
  height: 30px;
}
.normal-play
  .bottom
  .b-m
  .bar-wrapper
  .bar
  .bar-inner
  .bar-btn
  .bar-btn-content {
  position: relative;
  top: 7px;
  left: 7px;
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  border: 3px solid rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  background: #31c27c;
}
.normal-play .bottom .b-m .totalTime {
  text-align: right;
}
.normal-play .bottom .b-b {
  display: flex;
  align-items: center;
}
.normal-play .bottom .b-b div {
  flex: 1;
  color: #31c27c;
}
.normal-play .bottom .b-b div i {
  font-size: 30px;
}
.normal-play .bottom .b-b .left {
  text-align: right;
}
.normal-play .bottom .b-b .center {
  text-align: center;
  padding: 0 20px;
}
.normal-play .bottom .b-b .center i {
  font-size: 50px;
}
.normal-play .bottom .b-b .right {
  text-align: left;
}

.mini-play {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 300;
  background-color: #31c27c;
  height: 60px;
  display: flex;
  align-items: center;
}
.mini-play .head {
  flex: 0 0 40px;
  padding: 0 10px 0 20px;
}
.mini-play .head img {
  animation: rotate 10s linear infinite;
  border-radius: 50%;
}
.mini-play .head img.pause {
  animation-play-state: paused;
}
.mini-play .text {
  display: flex;
  flex-direction: column;
  line-height: 20px;
  flex: 1;
  overflow: hidden;
}
.mini-play .text h2 {
  margin-bottom: 2px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
  color: #fff;
}
.mini-play .text p {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 12px;
  color: #000;
}
.icon-mini.icon-play-mini,
.icon-mini.icon-pause-mini {
  font-size: 32px;
  position: absolute;
  left: 0;
  top: 0;
  color: #fff;
}
.icon-playlist {
  font-size: 30px;
  color: #fff;
}
.mini-play .control {
  flex: 0 0 30px;
  padding: 0 10px;
}

.play-list {
  position: fixed;
}
</style>



